import React from 'react'

const Result = ({correctResult,countCorrect,wrongResult}) => {
    return (
        <div className="results">
            <div className="title">
                <p>Correct answer:{countCorrect}</p>
            </div>
            <div className="resultsContainer">
                <div className="correctResults">
                   <p className="word"> Correct Word</p>
                    {correctResult.map((correctword,index)=>{
                        return(
                            <div key={index} className='row'>
                                <p>{correctword}</p>
                                </div>
                        )
                    })}
                </div>
                <div className="wrongResults">
                  <p className="word">  Wrong Word </p>
                   {wrongResult.map((wrongword,index)=>{
                       return(
                           <div key={index} className="row">
                               <p>{wrongword}</p>
                               </div>
                       )
                   })}
                </div>
            </div>
        </div>
    )
}

export default Result
