import React,{useState,useEffect} from 'react'
import Words from './component/Words'
import Container from './component/Container'
import Typeracer from './component/Typeracer'
import Result from './component/Result'
import './App.css'
const App=()=> {
  const [word,setword]=useState(Words);
  const [newWord,setnewWord]=useState(word[0])
  const [disabled,setdisabled]=useState(true);
  const [correctResult,setcorrectResult]=useState([])
  const [wrongResult,setwrongResult]=useState([])
  const [countCorrect,setcountCorrectt]=useState(0)
  const [time,settime]=useState(30)
  const [inputvalue,setinputvalue]=useState('')
  const [animation,setanimation]=useState(null)
   console.log(word.length)
  let randomword=Math.floor(Math.random()*word.length)

  const checkAnswer=()=>{
    if(inputvalue.trim() === newWord){
     setcorrectResult(prevcorrect=> [...prevcorrect,newWord])
     setcountCorrectt(prevcorrect=>prevcorrect+1)
     return;
    }
    setwrongResult(prevresult=>[...prevresult,inputvalue])
  }

  const handleInput=(e)=>{
    if(e.charCode===13 && inputvalue.trim()!==''){
      checkAnswer();
      setnewWord(word[randomword])
      setinputvalue('')
    }

  }

const handlestart=()=>{
  setdisabled(!disabled)
  setcorrectResult([])
  setwrongResult([])
  setcountCorrectt(0)
  setinputvalue('')

}
useEffect(()=>{
  if(time<=30 && time !==0 && disabled==false){
    setTimeout(()=>settime(prevtime=>prevtime-1),1000)
  }else if(disabled){
    settime(30)
    setanimation(null)
  }else if(time==0){
    setdisabled(true)
  }
  if(time<=10){
    setanimation("scaleNumber 2s infinite")
  }
},[disabled,time])

useEffect(()=>{
  setnewWord(word[randomword])
},[])

  return (
    <div className="App">
    <Container>
    <Typeracer
     newWord={newWord}
     inputvalue={inputvalue}
     setinputvalue={setinputvalue}
     disabled={disabled}
     time={time}
     animation={animation}
     handleInput={handleInput}
     handlestart={handlestart}

    />
     </Container>
   
    <Result
    correctResult={correctResult}
    wrongResult={wrongResult}
    countCorrect={countCorrect}
    />
    </div>
  );
}

export default App;
